/* =Global
****************************************************************************************/
body {}

html {}

.global-wrapper {
	position: relative;
	display: block;
	overflow-x: hidden;
}

h1 {
	font-size:30px!important;
	font-weight: normal;
	color: $darkgrey;
}

h2 {
	font-size: 20px!important;
	font-weight: bold;
	color: $brand-color-one;
}

h3 {
	font-size: 24px!important;
	font-weight: bold;
}

h4 {
	font-size:18px!important;
}

h5 {
	font-size:16px!important;
}

h6 {
	font-size:16px!important;
}

* {
	font-family: $brand-font-one;
}

h1,h2,h3,h4,h5,h6 {
	transition: .25s font-size ease-in-out;
}

p, li, table, address, span {
	font-size: 16px!important;
	line-height: 24px!important;
	color: $darkgrey;
	font-weight: 100;

	@media #{$large-device} {
		font-size: 16px!important;
		line-height: 24px!important;
	}
}

span {
}

a {
}

em {
	font-style: normal;
}

#wrapper {
	
}

#cms-main, .fixed-section {
	min-height: 200px;
	display: table;
	width:100%;
	max-width: $max-width;
	margin:auto;
}

.desktop-only {
	@media #{$large-device} {
		display: none!important;
	}
}

.mobile-only {
	@media #{$above-large-device} {
		display: none;
	}
}

#cms-main {
	background: white;
	padding: 35px 35px 60px;
	box-sizing: border-box;
	
	@media #{$large-device} {
		padding: 35px 15px 40px;
	}
}

.cms-banner {
	.cb-background {
		object-fit: cover;
		width: 100%;

		&.cb-desktop {
			@media #{$small} {
				display: none;
			}
		}

		&.cb-mobile {
			display: none;
			@media #{$small} {
				display: table;
			}
		}

	}
}

//Button Example
.brand-button {
	display: table;
	text-align: center;
	border-radius: 0;
	padding:13px 35px;
	font-weight: 500;
	font-size:1.35rem;
	text-decoration: none!important;	
	border: 2px solid;
	-webkit-backface-visibility: hidden;
	-webkit-transform: translate3D(0, 0, 0);
}

.button-one {
	background:white;
	color: black;
	border-color:white;
	transition:.25s box-shadow ease-in-out;
	box-shadow:inset 0px 0.01px 0px 0.01px #3d3d3d;
	
	&:hover {
		box-shadow:inset 0px 100px 0px 0px #3d3d3d;
		color:white;
	}
}


//Data-tip Tooltip
[data-tip] {
	position:relative;

}
[data-tip]:before {
	content:'';
	/* hides the tooltip when not hovered */
	display:none;
	content:'';
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-bottom: 5px solid #1a1a1a;	
	position:absolute;
	top:30px;
	left:0;
	right:0;
	margin:auto;
	z-index:8;
	font-size:0;
	line-height:0;
	width:0;
	height:0;
}
[data-tip]:after {
	display:none;
	content:attr(data-tip);
	position:absolute;
	top:35px;
	left:0;
	right:0;
	margin:auto;
	width: auto;
	padding:5px 8px;
	background:#1a1a1a;
	color:#fff;
	z-index:9;
	font-size: 0.75em;
	height:18px;
	line-height:18px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	white-space:nowrap;
	word-wrap:normal;
}
[data-tip]:hover:before,
[data-tip]:hover:after {
	display:table;
}