/* =Useful classes
*******************************************************************************/
.clearfix {
    &:before, &:after{ content:""; display:table; }
    &:after{ clear: both; }
}

.full-width {
  width:100%;
}
.site-button {
  background: $brand-color-two;
  color: white;
  width:100%;
  padding:10px;
  text-decoration: none;
  display:table;
  clear: both;
  text-align: center;
  font-size: .85rem;
  text-shadow: 0 0 0 white;
  transition:.25s all ease-in-out;

  &:hover {
    background: $brand-color-one;
  }
}

.date-circle {
  background: white;
  border: 1px solid $grey;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  border-radius: 100px;

  @media #{$large-device} {
    width: 100px;
    height: 100px;
  }

  strong {
      font-size: 50px;
      display: table;
      clear: both;
      color: $darkgrey;

      @media #{$large-device} {
        font-size: 30px;
      }
  }

  span {
      color: $brand-color-two!important;
      font-weight: bold;
      font-size: 20px!important;

      @media #{$large-device} {
        font-size: 14px!important;
      }
  }
}//date-circle

a {
  &.main-button {
    font-style: normal;
      font-weight: 500;
      font-size: 18px;
      text-decoration: none;
      color: $brand-color-two;
      border: 2px solid $brand-color-two;
      border-radius: 5px;
      display: table;
      padding: 5px 10px;
      margin: 15px auto 0;
      width: 100%;
      text-align: center;
      max-width: 150px;
      transition: .25s all ease-in-out;
  
      &:hover {
          background: $brand-color-two;
          color: white;
      }
  }
}

.max-width {
  max-width:$max-width;
  margin:auto;
}

.pad-hit {
  @include pad-hit
}

.fadein {
	-webkit-animation: fadein 0.7s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
	  animation: fadein 0.7s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;

}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 1s linear 2;
  -moz-animation: rotating 1s linear 2;
  -ms-animation: rotating 1s linear 2;
  -o-animation: rotating 1s linear 2;
  animation: rotating 1s linear 2;
}