/* =Variables
****************************************************************************************/
@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700&display=swap');

//Base Fonts
$brand-font-one: 'PT Sans', sans-serif;
$brand-font-two: 'PT Sans', sans-serif;

//Base Theme Colors
$grey: #58595b;
$darkgrey: #58595b;
$offwhite: #f0f0f0;

//Viewport Stuff
$max-width: 1200px;
$body-background: white;
$brand-color-one: #1a5a73;
$brand-color-two: #248ea6;
$brand-color-three: #253b59;
$brand-color-four: #96d9c6;
$blue-one: #1a5a73;
$blue-two: #248ea6;
$blue-three: #253b59;
$blue-four: #96d9c6;
$green: #96d9c6;
$red: #c80000;

//Banner Stuff
$cms-page-banner-min-height: 500px;
$cms-page-banner-min-device-height: 400px;
$cms-page-banner-min-mobile-height: 300px;

//Default Font Sizes
$h1-font-size-desktop: 50px;
$h2-font-size-desktop: 48px;
$h3-font-size-desktop: 36px;
$h4-font-size-desktop: 28px;
$h5-font-size-desktop: 22px;
$h6-font-size-desktop: 18px;
$p-font-size: 14px;
$span-font-size: 14px;
$a-font-size: 14px;

//Default Text Colors
$h1-color: black;
$h2-color: black;
$h3-color: black;
$h4-color: black;
$h5-color: black;
$h6-color: black;
$p-color: black;
$span-color: black;
$a-color: black;
$a-hover-color: black;

//Base Font Styles
$base-text-size: 12px;

//General Form Styles
$form-background-color: white;


