//IGNORE UNLESS OVERRIDE NEEDED - Seamless Adjustments to breakpoints, etc.
$h1-font-size-tablet: $h1-font-size-desktop * .75;
$h2-font-size-tablet: $h2-font-size-desktop * .75;
$h3-font-size-tablet: $h3-font-size-desktop * .75;
$h4-font-size-tablet: $h4-font-size-desktop * .75;
$h5-font-size-tablet: $h5-font-size-desktop * .75;
$h6-font-size-tablet: $h6-font-size-desktop * .75;
$h1-font-size-mobile: $h1-font-size-desktop * .5;
$h2-font-size-mobile: $h2-font-size-desktop * .5;
$h3-font-size-mobile: $h3-font-size-desktop * .5;
$h4-font-size-mobile: $h4-font-size-desktop * .5;
$h5-font-size-mobile: $h5-font-size-desktop * .5;
$h6-font-size-mobile: $h6-font-size-desktop * .5;
$p-line-height: $p-font-size * 1.5;
$span-line-height: $span-font-size * 1.5;
$a-line-height: $span-font-size * 1.5;