/* =Minimal base appearance
*******************************************************************************/
html,
body {
    height: 100%;
    min-width: 350px;
}

body {
    width: 100%;
}

select, input, textarea {
    color:$darkgrey;
}

a {
    color: $brand-color-one;
    font-weight: 500;
    text-decoration: underline;
    transition: .25s all ease-in-out;

    &:hover {
        color: $brand-color-two;
        text-decoration: none;
	}
}
::-moz-selection { color: #fff; background: $brand-color-one; }
::selection      { color: #fff; background: $brand-color-one; }

img {
    max-width: 100%;
    height: auto;
}
