/* =Form
****************************************************************************************/
//Form Container Values
$form-background-color: white;
$form-padding: 0;
$form-text-color: $grey;
//Form Input Values
$form-input-background-color: white;
$form-input-text-color: black;
//Submit Button Values
$form-submit-background-color: $brand-color-one;
$form-submit-text-color: white;
$form-submit-hover-background-color: $brand-color-two;
$form-submit-hover-text-color: white;

.form-container {
    display: block;
    width:100%;
    margin:25px auto 0;

    .form {
        width:100%;
        background:none;
        font-family: $brand-font-one;
        font-weight: 300;

        form {
            display: table;
            width: 100%;
            clear: both;
        }
    
        .form-row {
            width:100%;
            display: flex;
            flex-basis:100%;
            flex-direction:row;
            justify-content: space-between;
    
            @media #{$under-tablet} {
                flex-direction: column;
            }
    
            .form-col-1 {
                width:100%;
            }
    
            .form-col-2 {
                width:49%;
                float: left;
    
                @media #{$under-tablet} {
                    width:100%;
                }
            }
    
            .form-col-3 {
                width:32%;
                float: left;
    
                @media #{$under-tablet} {
                    width:100%;
                }
            }
        }
    
        label {
            display: block;
            clear: both;
            color: $brand-color-two;
            margin-bottom: 0;
            padding-left: 0;
            font-weight: 500;

            @media #{$under-tablet} {
                padding-left: 0;
            }
        }
    
        input[type="text"], input[type="email"], input[type="tel"], textarea, select  {
            width:100%;
            margin-bottom:25px;
            border-radius: 7px;
            color:$form-input-text-color;
            background:$form-input-background-color;
            padding:15px;
            border:1px solid rgba(0,0,0,0.25);
            font-family: $brand-font-one;
            font-weight: 500;
        }

        textarea {
            resize: none;
        }

        select {
            -webkit-appearance: none;
            background:url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAYAAAAf8/9hAAAABmJLR0QA/wD/AP+gvaeTAAAAY0lEQVQ4je3MOw6CQBSG0RMzW4DEDVINO3IpFNZ2SsJqKGgGQ6HzsJU/ud09H+cg4Ia+wfTJhAtWPHDHtQJ3mPBM9r0BSyHS4YXx20MuUsS5SDX+FGnG+yLmdLEVHyM/43/ZBhmLFKZf0673AAAAAElFTkSuQmCC");
            background-repeat: no-repeat;
            background-position: center right 20px;
        }
    
        input[type="submit"] {
            width:100%;
            border:0;
            padding:10px;
            border-radius: 7px;
            color:white;
            background: $brand-color-two;
            box-shadow:inset 0 0 0 0.01px $brand-color-one;
            transition:.25s all ease-in-out;
            font-family: $brand-font-one;
            font-weight: bold;
            text-transform: uppercase;
            max-width: 200px;

            @media #{$under-tablet} {
                max-width: 100%;
            }
    
            &:hover {
                box-shadow:inset 0 100px 0  $brand-color-one;
                color: white;
            }
        }
    }
}

.drop_uploader.drop_zone {
    border-radius: 0!important;
    border: 1px solid $grey!important;
    .text {
        a {
            color: $brand-color-one;
            text-decoration: none;
        }
    }
}

.wpcf7-checkbox {
    .wpcf7-list-item {
        margin:0 10px 0 0;
        @media #{$under-tablet} {
            width: 100%;
        }

        &.first {
            margin-left:0;
            margin-top: 10px;

            @media #{$under-tablet} {
                margin-right:10px;
            }
        }
    }

    input[type=checkbox] {
        -webkit-appearance: none;
        background:white;
        border:2px solid $grey;
        width:20px;
        height:20px;
        margin-right:5px;
    }

    input[type=checkbox]:checked {
        background:$brand-color-one;

    }
}

.wpcf7-radio {
    display: table;

    .wpcf7-list-item {
        margin:0 10px 14px 0;
        @media #{$under-tablet} {
            width: 100%;
        }

        &.first {
            margin-left:0;
            margin-top: 10px;

            @media #{$under-tablet} {
                margin-right:10px;

            }
        }
    }

    input[type=radio] {
        -webkit-appearance: none;
        background:white;
        width:15px;
        height:15px;
        border-radius: 10px;
        margin-right:5px;
    }

    input[type=radio]:checked {
        background:$brand-color-one;

    }
}


.form-base {

}

.form-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    background: rgba(0,0,0,0.7);
    opacity: 0;
    transition: .5s opacity ease-in-out;
    z-index: 9;

    &.active-backdrop {
        @media #{$large-device} {
            display: block;
            opacity: 1;

        }
    }
}

.slide-form-container {
    display: block;
    width:100%;
    margin:0 auto;
    z-index: 99;

    @media #{$above-large-device} {
        position: absolute;
        margin: auto;
        left: 0;
        right: 0;
        width: 100%;
        display: block;
        transition: .25s max-height ease-in-out;
    }

    @media #{$large-device} {
        position: fixed;
        height: 100%;
        top: 0px;
        background: none;
        z-index: 99;
        display: none;
        opacity: 0;
        transition: .25s opacity ease-in-out;
    }

    &.active-form {
        opacity: 1;
        display: table;
    }

    div.wpcf7 {
        padding: 0;
        margin: 0 0 0 auto;
        width: auto;
        display: table;

        @media #{$large-device} {
            margin: 0 auto;
        }
    }

    .form {
        width:100%;
        background:none;
        font-family: $brand-font-one;
        font-weight: 300;
        float: right;
        position: relative;
        height: 1px;

        @media #{$large-device} {
            float: none;
            margin: 75px auto 0;
            height: auto;
            position: relative;
            max-width: 350px;
        }

        .close-form {
            position: absolute;
            display: block;
            top: 10px;
            right: 10px;
            font-size: 40px;
            line-height: 30px;
            transform: rotate(135deg);
            cursor: pointer;
            color: white;
            width: 30px;
            height: 30px;
            border-radius: 30px;
            border: 1px solid white;
            text-align: center;
            transition: .25s all ease-in-out;

            &:hover {
                background: $brand-color-one;
            }

            @media #{$above-large-device} {
                display: none;
            }
        }

        form {
            display: table;
            width: 100%;
            max-width: 400px;
            background: $brand-color-three;
            padding:25px;
            clear: both;

            @media #{$large-device} {
                background: $brand-color-two;
            }
        }

        h3 {
            color: white;
            text-align: center;
            line-height: 32px;
            margin-bottom: 15px;
            font-size: 28px!important;
        }

        .lets-talk-btn {
            @media #{$large-device} {
                text-align: center;
            }
        }
    
        .form-row {
            width:100%;
            display: flex;
            flex-basis:100%;
            flex-direction:row;
            justify-content: space-between;
    
            @media #{$under-tablet} {
                flex-direction: column;
            }
    
            .form-col-1 {
                width:100%;
            }
    
            .form-col-2 {
                width:49%;
                float: left;
    
                @media #{$under-tablet} {
                    width:100%;
                }
            }
    
            .form-col-3 {
                width:32%;
                float: left;
    
                @media #{$under-tablet} {
                    width:100%;
                }
            }
        }
    
        label {
            display:block;
            clear:both;
            color:$brand-color-one;
            margin-bottom:0;
            padding-left: 0;

            @media #{$under-tablet} {
                padding-left: 0;
            }
        }

        span.wpcf7-not-valid-tip {
            color: $red;
            font-size: 1em;
            font-weight: normal;
            display: block;
            position: absolute;
            margin-top: -24px;
            margin-left: 2px;
            font-size: 12px!important;
        }

        div.wpcf7-validation-errors, div.wpcf7-acceptance-missing {
            border: 2px solid #f7e700;
            position: absolute;
            margin-top: -10px;
            margin-left: -16px;
            background: white;
            text-align: center;
        }
    
        input[type="text"], input[type="email"], input[type="tel"], textarea, select  {
            width:100%;
            margin-bottom:20px;
            color:$form-input-text-color;
            background:$form-input-background-color;
            padding:10px 20px;
            border:1px solid $grey;
            font-size: 20px;
            font-family: $brand-font-one;
            font-weight: 300;
        }
    
        input[type="submit"] {
            width:100%;
            border:0;
            padding:10px;
            color:white;
            font-size: 22px;
            background: $red;
            box-shadow:inset 0 0 0 0.01px $brand-color-one;
            transition:.25s all ease-in-out;
            font-family: $brand-font-one;
            font-weight: bold;
            border-radius: 5px;

            @media #{$under-tablet} {
                max-width: 100%;
            }
    
            &:hover {
                box-shadow:inset 0 100px 0  $brand-color-one;
                color: white;
            }
        }
    }
}

