/* =Subpages
****************************************************************************************/
.page-title {
    color: $darkgrey;
    margin: 0;
    font-family:$brand-font-one;
    font-weight: normal;
    text-align: left;
    font-weight: 100;
    position: relative;
    padding-bottom: 20px;

    @media #{$large-device} {
        font-size: 26px!important;
    } 
}

.cms-banner {
    .cb-container {
        .cb-background {
            object-fit:cover;
            object-position: center;
            width: 100%;
            height: 100%;
            max-height: 550px;

            @media #{$small} {
                min-height: 225px;
                max-height: 225px;
            }
        }
    }
}

.cms-content-container {
    color: $darkgrey;
    
    @media #{$above-large-device} {
        max-width: 65%;
    }

    @media #{$large-device} {
        padding-left:0;
        padding-right:0;
    }

    &.single-article {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        @media #{$large-device} {
            flex-direction: column;
        }

        .left-side {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            width: 20%;
            padding-right: 3%;

            @media #{$large-device} {
                width: 100%;
                padding-right: 0;
            }

            .main-button {
                max-width: 175px;
                margin-top: 20px;
            }
        }

        .right-side {
            display: flex;
            flex-direction: column;
            width: 80%;
            padding-left: 3%;
            border-left: 1px solid $grey;

            @media #{$large-device} {
                width: 100%;
                padding-left: 0;
                border-left: 0;
                margin-top: 25px;
            }
        }
    }

    h2 {
        color:$brand-color-one;
        font-weight: 500;
        margin-bottom: 0;
        margin-top: 0;

        &:first-child {
            margin-top: 0;
        }
    }

    h3 {
        color: $brand-color-two;
    }
	.single-column {
		display: table;
        padding:0 0 25px;
        width: 100%;

        p {
            color: $darkgrey;
            font-size: 16px;
            line-height: 20px;
        }

        .sc-col {
            h3 {
                text-decoration: none!important;
                color: $red;
                a {
                    margin-top: 40px;
                    margin-bottom: 5px;
                    display: table;
                    color: $brand-color-one;
                    font-size: 1.25rem;
                    text-decoration: none!important;

                    &:hover {
                      color: $brand-color-two;
                    }
                }
            }

        }
	}

	.double-column {
		display: table;
		padding:0 0 25px;
        color: $darkgrey;
	}

    p:empty {
        display: none;
    }

    div {
        &:last-child {
            margin-bottom: 0;
            padding-bottom: 0;
        }
    }

	ul {
		list-style: none;
		padding:0 0 25px;
		padding-left:15px;
      

		li {
            color:$darkgrey;
            font-family:$brand-font-one;

			&:before {
                content:'-';
                color: $darkgrey;
                padding-right: 10px;
				margin-left:-15px;
			}
			margin-bottom:5px;
		}
	}

	.dc-col {
		width:46%;
		margin:0 2%;
		float: left;
		&:first-child {
			margin-left:0;
			@media #{$under-tablet} {
				margin-bottom:25px;
			}
		}

		&:last-child {
			margin-right:0;
		}

        @media #{$under-tablet} {
			width:100%;
			margin:0;
		}
	}

	.cms-cc-img {
		width:100%;
		margin:0 auto 25px;
	}


	.accordion:before {
		display: none!important;
    }

    .image-gallery {
        display: flex;
        flex-direction: row; 
        flex-wrap: wrap; 
        justify-content: space-between;

        @media #{$large-device} {
            flex-direction: column;
            align-items:center;
        }
        &__cell {
            width:23%;
            @media #{$large-device} {
                width:100%;
                clear:both;
                margin:0 auto 10px;
                float:none;
            }

            img {
                max-height: 150px;
            }
        }
    }
			

    #general-accordion {
        background: #ffffff;
        position: relative;
            margin-bottom: 25px;
        color: $darkgrey;


        &:before {
            content:'';
            width:100%;
            height:100%;
            max-width:33%;
            top:0;
            display:block;
            background:$brand-color-two;
            color: white;
            position: absolute;


                @media screen and (max-width:767px) {
                    display:none;
                }

        }

        #ga-container {
            display: table;
            width: 100%;
            background: 0;
            padding:0;
            max-width: 1400px;
            height: auto;
            margin: auto;

            #sac-quarter {
                width:25%;
                padding-right:3%;
                float: left;
                position: relative;
                color: white;

                @media screen and (max-width:767px) {
                    width:100%;
                    padding-right: 0;
                    color:$darkgrey;
                }
            }

            #sac-three-quarter {
                width:75%;
                float: left;
                padding-left:5%;

                @media screen and (max-width:767px) {
                    width:100%;
                    padding-left: 0;
                }
            }

            .sac-highlight {
                padding:0;

                img {
                    padding-bottom:25px;
                }

                h3 {
                    font-size:2rem;
                    @media screen and (max-width:767px) {
                        color:$brand-color-two;
                    }
                }

                p {
                    margin:10px 0;
                    font-size: 1rem;
                    font-weight: 400;
                    line-height: 1.5rem;
            color: $darkgrey;
                }
            }

            .ga-ul-container {
                padding:0;

                ul.ga-ul {
                    display: table;
                    width:100%;
                    background:#fff;
                    color: $darkgrey;
                    padding-left:0;


                    .ga-li {
                        position: relative;
                        width: 100%;
                        padding:0;
                        border-top:1px solid #d9d9d9;
                        background:transparent;
                        display: block;
                        max-height: 50px;
                        margin-bottom:2px;
                        overflow: hidden;
                        transition:.25s max-height ease-in-out, .25s background ease-in-out, .35s box-shadow ease-in-out;

                        &:before {
                            display: none;
                        }

                        &:nth-child(odd) {
                            background:$brand-color-one;
                        }

                        &:nth-child(even) {
                            background:$brand-color-two;
                        }

                        .ga--title {
                            color:white;
                            font-size:16px;
                            padding:15px 25px;
                            position: relative;
                            transition:.25s color ease-in-out;
                            cursor: pointer;


                            @media screen and (max-width:480px) {
                                font-size:14px;
                            }

                            .ga--arrow {
                                position:absolute;
                                top:10px;
                                right:15px;
                                width:30px;
                                height:30px;
                                transform: rotate(-90deg);
                                transition: .25s transform ease-in-out;

                                img {
                                    width:100%;
                                }
                            }


                        }

                        .ga--content {
                            padding: 5px 25px 15px 25px;
                            background:white;

                            ul {
                              columns: 2;
                              -webkit-columns: 2;
                              -moz-columns: 2;
                                color: $darkgrey;


                              li {
                                  padding-left:20px;

                                  &:before {
                                      content:'';
                                      background:$brand-color-two;
                                      display: inline-block;
                                      width:6px;
                                      height:6px;
                                      margin-left:-18px;
                                      margin-top:7px;
                                      position: absolute;
                                        border-radius: 50%;
                                  }
                              }

                            }


                            p {
                                margin:10px 0;
                                line-height:1.5rem;
                                font-weight: 400;
                                color: $darkgrey;
                            }

                            a {
                                // color:$brand-color-one!important;
                                // background: none!important;
                            }

                            ul {
                                margin-bottom:5px;
                                columns: 1;
                                -webkit-columns: 1;
                                -moz-columns: 1;

                                li {
                                    margin-bottom: 10px;
                                    font-size: 1rem;


                                    ul {
                                        clear:both;
                                        display: table;


                                        li {
                                            clear: both;
                                            display: table;
                                            width: 100%;
                                            margin:5px 0;
                                            font-weight: normal;

                                            &:before {
                                                content:'-';
                                                background:white;
                                                margin-top: 0px;
                                            }
                                        }
                                    }
                                }
                            }
                        }


                    }

                    .expand-ga-item {
                        max-height:9001px;
                        background:$brand-color-two;
                        color: white;

                        .ga--title {
                            color:white;

                            .ga--arrow {
                                transform: rotate(0deg);
                            }
                        }

                        .ga--content {
                            border-top:1px solid #d9d9d9;

                            a {
                                color: $brand-color-one;
                            }

                            p {
                                color: $darkgrey;
                                font-weight: 400;
                            }
                        }

                    }
                }

            }


        }
    }
}

.no-banner {
    position: relative;
    height: 1px;
}

.page-id-87 {
    .cms-content-container {
        max-width: 100%;
    }
}

ul.team-list {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding-left: 0;
    padding-bottom: 0;

    @media #{$large-device} {
        flex-direction: column;
    }

    li.team-member {
        float: left;
        width: 23%;

        @media #{$large-device} {
            width: 100%;
            max-width: 300px;
            display: table;
            margin: 0 auto 50px;
        }

        &:last-child {
            @media #{$large-device} {
                margin-bottom: 0;
            }
        }

        &:before {
            display: none;
        }

        .team-image-wrapper {
            position: relative;
        }
        .close-it {
            position: absolute;
            top: 25px;
            right: 25px;
            width: 34px;
            height: 34px;
            cursor: pointer;

            img {
                width: 34px;
                height: 34px;
                opacity: 1;
                cursor: pointer;
                transition: .25s all ease-in-out;

                &:hover {
                    opacity: .5;
                }
            }
        }
        .team-member-info {
            display: none;
            margin: 0 auto;
            position: relative;
            background: white;
            padding: 50px 100px;
            border-radius: 5px;
            width: 90%;
            max-width: $max-width;
            justify-content: space-between;
            position: fixed;
            top: 50%;
            z-index: 99;
            left: 50%;
            transform: translate(-50%,-50%);

            @media #{$large-device} {
                width: 90%;
                flex-direction: column;
                padding: 50px;
            }

            &__left {
                width: 20%;

                @media #{$large-device} {
                    width: 100%;
                }

                img {
                    @media #{$small} {
                        max-width: 150px;
                    }
                }
            }

            &__right {
                width: 75%;

                @media #{$large-device} {
                    width: 100%;
                }

                h3, span, p {
                    margin: 0;
                }

                h3 {
                    margin-bottom: 5px;

                    @media #{$small} {
                        margin-bottom: 0;
                    }
                }

                

              

                span {
                    margin-bottom: 20px;

                    @media #{$small} {
                        margin-bottom: 5px;
                    }
                }

                p {
                    @media #{$large-device} {
                        font-size: 14px!important;
                    }

                    @media #{$small} {
                        font-size: 13px!important;
                        max-height: 200px;
                        overflow-y: scroll;
                    }

                    &::-webkit-scrollbar {
                        @media #{$small} {
                            -webkit-appearance: none;
                            width: 8px;
                        }
                    }
                    &::-webkit-scrollbar-thumb {
                        @media #{$small} {
                            border-radius: 0;
                            background-color: $brand-color-two;
                        }
                    }

                    &::-webkit-scrollbar-track { 
                        @media #{$small} {
                            -webkit-appearance: none;
                            background-color: #d9d9d9;
                        }
                    }
                }

                a {
                    margin-top: 15px;
                    display: table;
                }
            }

            &.active-member {
                display: flex;
            }

            a {
                color: $brand-color-two;
                text-decoration: none;
            }
            
        }

        h3 {
            color: $brand-color-two;
            font-size: 24px;
            font-weight: 500;
            display: table;
            margin: auto;
            padding-top: 10px;
         
        }

        span {
            color: $darkgrey;
            display: table;
            margin: auto;
            word-break: break-word;
            padding-top: 5px;
            @media #{$large-device} {
                font-size: 18px!important;
            }
        }

        em {
            display: table;
            margin: auto;
            font-size: 14px;
        }

     

        img {
            width: 100%;
            margin: auto;
            display: table;

            @media #{$large-device} {
                max-width: 220px;
            }
        }
    }
}

.about-bottom {
    display: table;
    width: auto;
    margin: auto;
    font-weight: bold;
    font-size: 22px;
    line-height: 28px;
    margin-top: 30px;
    color: $brand-color-one;
}

//news log
.news-log {
    &__item {
        display: flex;
        flex-direction: row;
        flex-basis: 100%;
        justify-content: space-between;
        width: 100%;
        clear: both;
        margin-bottom: 25px;

        @media #{$tablet} {
            flex-direction: column;
            margin-bottom: 50px;
        }

        &:last-child {
            margin-bottom: 0;
            padding-bottom: 0;
        }

        &___image {
            width: 30%;

            @media #{$tablet} {
                width: 100%;
                margin-bottom: 15px;
            }
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;

                @media #{$tablet} {
                    max-height: 200px;
                }
            }
        }


        &___details {
            width: 65%;

            @media #{$tablet} {
                width: 100%;
            }
            .log-name {
                font-size: 22px!important;
                font-weight: bold;
                margin-top: 5px;
                color:$brand-color-two;

                @media #{$tablet} {
                    font-size: 24px!important;
                }
            }

            .log-date {
                color: $darkgrey;
                font-weight: 300;
                font-size: 14px!important;
                line-height: 18px!important;

                @media #{$tablet} {
                    font-size: 18px!important;
                }


            }

            p {
                display: table;
                margin: auto;
                font-size: 14px!important;
                line-height: 18px!important;
                margin-top: 10px;
                font-weight: 300;
                width: 100%;
                color: $darkgrey;
                @media #{$tablet} {
                    font-size: 18px!important;
                    line-height: 24px!important;
                }
            }

            .log-link {
                text-decoration: none;
                color: $brand-color-two;
                display: table;
                margin-top: 20px;
                font-size: 18px;
                font-weight: bold;

                @media #{$tablet} {
                    font-size: 20px!important;
                }
                

                &:hover {
                    color: $darkgrey;
                }
            }
        }

    }
}

//contact us
.contact-us {
    display:flex;
    flex-basis: 100%;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 25px;

    @media #{$large-device} {
        flex-direction: column-reverse;
        padding-top: 0;
    }

    &__column {
        color: $darkgrey;

        .form-container {
            margin-top: 0;
        }

        &:first-child {
            width: 48%;
        }

        &:last-child {
            width: 48%;

            @media #{$large-device} {
                margin-bottom: 25px!important;
            }
        }
        
        @media #{$large-device} {
            width: 100%!important;
        }

        iframe {
            width: 100%;
            height: 400px;
        }
        &___divi {
            width: 100%;
            height: 1px;
            background: $grey;
            display: table;
            margin: 0 0 15px;

            @media #{$large-device} {
                display: none;
            }
        }

        &___line {
            display: table;
            clear: both;
            min-height: 28px;
            padding-left: 0;
            margin-bottom: 15px;

            @media #{$large-device} {
                margin: 0 auto 5px;
                text-align: center;
            }
        }

        h2 {
            font-size: 18px!important;
            font-weight: bold;
            width: 100%;
            color: $darkgrey;

            @media #{$large-device} {
                display: table;
                margin: auto;
                text-align: center;
                padding-top: 0px;
            }
        }

        h3 {
            color: $brand-color-two;
            font-weight: normal;
            font-style: normal;

            @media #{$large-device} {
                text-align: center;
                margin: auto;
            }
        }

        h4 {
            @media #{$large-device} {
                text-align: center;
                margin: auto;
            }
        }

        a {
            text-decoration: none;
            color: $darkgrey;
            display: table;
            clear: both;
            position: relative;
            font-weight:500;

            @media #{$large-device} {
                margin: 0 auto;
            }
        }

        .contact-a {
            font-weight: bold;
            padding-left: 25px;
            i {
                color: $brand-color-two;
                position: absolute;
                left: 0;
            }
        }

        span {
            color: $darkgrey;
            font-weight: 500;
            font-size: 15px!important;
            line-height: 22px!important;
        }

        .social-list {
            padding: 0;
        }
    }
}

.map-box {
    display: table;
    margin-top: 50px;
    width: 100%;

    @media #{$large-device} {
        margin-top: 25px;
    }

    iframe {
        width: 100%;
        height: 400px;
    }
}

.icontacts {
