    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;

    @media #{$large-device} {
        flex-direction: column;
    }

    &__item {
        width: 31%;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media #{$large-device} {
            width: 100%;
        }

        span {
            font-weight: 500;
            display: table;
            margin: 10px 0 0;
        }

       ul {
           padding-bottom: 0;

            li {
                margin-bottom: 0;
                font-size: 14px!important;
                font-weight: 500;
            }
       }//ul
    }//item
}//associations