/* =Header
****************************************************************************************/
$dropdown-parent-text-color: white;
$dropdown-parent-background-hover-color: $brand-color-one;
$dropdown-parent-text-hover-color: white;

$dropdown-child-background-color: $brand-color-one;
$dropdown-child-background-hover-color: black;
$dropdown-child-text-color: white;
$dropdown-child-text-hover-color: white;

$dropdown-mobile-background-color: $brand-color-one;
$dropdown-mobile-text-color: white;

.header {
  box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
  position:relative;
  width: 100%;
  z-index: 3;
  background: $brand-color-one;

  @media #{$above-large-device} {
    position:relative;
    display:table;
  }

  .top-head {
    display: table;
    width:100%;
    padding-top:15px;
    padding-bottom: 15px;

    @media #{$large-device} {
      padding: 0;
      display: flex;
      align-items: center;
      height: 85px;
    }

    &__left-side {
      float: left;

      @media #{$large-device} {
        display: table;
        width: 70%;
      }

      @media #{$small} {
        width: 70%;
      }
      
    }

    &__right-side {
      float: right;
      margin-top:15px;

      @media #{$large-device} {
        display: table;
        width: 25%;
        margin-top: 0;
        margin-right: 65px;
      }
      @media #{$small} {
        width: 15%;
        margin-right: 60px;
        display: flex;
        justify-content: flex-end;
      }


      .social-head {
        @include social-head;

        @media #{$small} {
          display: flex;
          justify-content: flex-start;
        }

        li { 
          &.social-mobile-phone {
            @media #{$large-device} {
              display: flex;
              order: 1;
            }
          }

          &.social-mobile-marker {
            @media #{$large-device} {
              display: flex;
              order: 2;
              margin-right: 0;
            }
          }
        }

        &__divider {
          font-size: 0;
          background: $brand-color-four;
          width: 2px;
          height: 50px;
          font-size: 0;
          color: white;
          margin: 0 15px;

          @media #{$large-device} {
            display:none!important;
          }
        }

      
        i {
          &.fa-map-marker-alt {
            position: absolute;
          }
        }
        span {
        }
        
      }//social-head
    }//right-side

    
  }//top-head
  .head-contain-mobile-items {
    display: none;
    @include pad-hit;
    @media #{$large-device} {
      display: flex;
      flex-direction: row;
    }

    .social-head-three {
      @media #{$small} {
        display: flex;
        align-items: center;
      }
    }
  }

  .head-contain {
    display:table;
    width:100%;
    background-color: $brand-color-two;
    //padding:15px 0;
    //box-shadow: inset 0 7px 9px -7px rgba(0,0,0,0.35);

    &__lets-talk {
        display: flex;
        order: 1;
        width: 40%;
        align-items: flex-end;
    }

    &__social-side {
        @include social-head;
        display: flex;
        order: 2;
        width: 100%;
        clear: both;
        padding-top:10px;
        padding-bottom:10px;
      

      li {
        margin-right: 0;
      }

    }//social-side
  }//head-contain

  .logo {
    display: block;
    float: left;
    font-size: 2em;
    padding: 10px 20px;
    text-decoration: none;
    max-width: 300px;

    @media #{$above-large-device} {
      padding-left: 0;
    }
    @media #{$large-device} {
      max-width:250px;
    }
  }

 

  ul#menu-main-menu {
    transition: max-height .2s ease-out;
    z-index: 99;
  
    @media #{$above-large-device} {
      clear: none;
      margin: auto;
      max-height: none;
      width: auto;
      display: table;
    }

    @media #{$large-device} {
      clear: both;
      max-height: 0;
      position:absolute;
      overflow: hidden;
      top:130px;
      width:100%;
      padding-bottom: 0;
      padding-top: 0;
    }
    > li {
      @media #{$above-large-device} {
        float: left;
        position: relative;
        margin-right: 10px;
        padding-top: 5px;
        padding-bottom: 5px;
        width: auto;
        text-align: center;
      }

      @media #{$large-device} {
        text-align: center;
        border-bottom: 1px solid $brand-color-two;
      }

      &:before {
        @media #{$above-large-device} {
          content:'';
          position: absolute;
          width: 100%;
          height: 0px;
          bottom:0;
          left:0;
          background: $brand-color-one;
          transition: .25s all ease-in-out;
        }
      }

      &:hover, &.current-menu-item, &.current-page-ancestor {
        &:before {
          @media #{$above-large-device} {
            height:8px;
            background: $brand-color-one;
          }
        }
      }

      a {
        display: block;
        text-decoration: none;
        color: white;
        transition:.25s all ease-in-out;
        background: none;
        font-size: 18px;
        font-weight: bold;
        text-transform: uppercase;

        @media #{$above-large-device} {
          padding: 15px;
        }

        @media #{$large-device} {
          color: $brand-color-one;
          background: white;
          padding: 20px;
          font-size: 24px;
          text-transform: none;
          font-weight: normal;
        }

        @media #{$small} {
          padding: 10px 20px;
          font-size: 24px;
        }

        &:hover {
          background: none;
          @media #{$large-device} {
            background: $brand-color-two;
            color: white;
          }
        }

      }

      &:last-child {
        a {
          //padding-right: 0;
        }
      }

      ul {
        display:none;
        @media #{$above-large-device} {
          position:absolute;
          top:100%;
          min-width: 250px;
          box-shadow: 0 0 5px rgba(0,0,0,0.25);
        }
        @media #{$large-device} {
          width:100%;
        } 
    
        li {
          display:table;
          width:100%;
          clear:both;
          padding-bottom: 0;

          &:after {
            display: none;
          }

          a {
            border-bottom:1px solid white;
            color:$darkgrey;
            background:white;
            box-shadow:inset 0 0 0 0.01px $brand-color-one;
            transition:.25s all ease-in-out;
            padding: 10px;
            text-align: left;
            font-size: 14px;

            @media #{$large-device} {
              background:$brand-color-two;
              color: white;
              border: none;          
              padding-left: 0px;
              font-size: 18px;
              padding: 5px;
              text-align: center;
            }

            &:after {
              display: inline;
            }
          }

          &:last-child {
            a {
              border-bottom: 0;
            }
          }

          &:hover {
            a {
              background:$brand-color-one;
              box-shadow:inset 0px 100px 0px $brand-color-one;
              color:white;
            }
          }
        }
      }
    }
  }

  .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 35px 15px;
    position: relative;
    user-select: none;

    @media #{$above-large-device} {
      display: none;
    }

    @media #{$large-device} {
      position: absolute;
      top: 5px;
      right: 0;
    }

    .navicon {
      background: white;
      display: block;
      height: 4px;
      position: relative;
      transition: all .2s ease-out;
      width: 30px;

      &:before, &:after {
        background: white;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        transition: all .2s ease-out;
        width: 100%;
      }

      &:before {
        top: 10px;
      }

      &:after {
        top: -10px;
      }
    }
  }

  .menu-btn {
    display: none;
  }

  .menu-btn:checked ~ .menu {
    max-height: 650px!important;
    overflow: scroll;
  }

  .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;

    &:before {
      transform: rotate(-45deg);
    }

    &:after {
      transform: rotate(45deg);
    }
  }

  .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before, .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
}//.header


.header li a:hover,
.header .menu-btn:hover {
  background-color: #f4f4f4;
}


.bottom-mobile-bar {
  background: $brand-color-two;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0;
  
  @media #{$above-large-device} {
    display: none;
  }
  div {
    display: flex;
    align-items: center;
    padding: 10px 0;


    &.left-side {
      width: 55%;
      padding-left: 15px;
      a {
        text-decoration: none;
        color: white;
        font-size: 20px;


        &:hover {
          background: none;
          color: $brand-color-one;
        }
      }
    }

    &.right-side {
      width: 40%;
      padding-right: 15px;
      background: rgba(0,0,0,0.15);
      justify-content: flex-end;

      .social-head {
          display: flex;
          justify-content: flex-end;

          &__divider {
            font-size: 0;
            background: $brand-color-four;
            width: 2px;
            height: 20px;
            font-size: 0;
            color: white;
            margin: 0 0 0 15px;
          }

          a {
            color: white;
            padding: 0 10px;
            font-size: 20px;
            display: table;
            text-decoration: none;

            &:hover {
              background: none;
              color: $brand-color-one;
            }
          }

          &:last-child {
            a {
              padding-right: 0;
            }
          }
        
      }
    }
  }
}