/* =Home
****************************************************************************************/
#information-block {
	position: relative;
	background: white;
	padding: 35px 0;
	box-sizing: border-box;

	@media #{$large-device} {
		padding: 20px;
	}

	#ib-container {
		@include pad-hit;
	}

	h1 {
		padding-bottom: 30px;
		text-align: center;
		margin: 0 auto 35px;
		max-width: 800px;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			height: 4px;
			background: $brand-color-two;
			display: block;
			width: 100%;
			max-width: 150px;
		}
	}

	p {
		color: $darkgrey;
	}
}

#carousel-block {
	background:white;
	position: relative;

	button.flickity-button {
		@media #{$large-device} {
			display: none;
		}
	}

}

#home-carousel {
	position: relative;

	.carousel-cell {
		img {
			max-height: 550px;
			object-fit: cover;
			object-position: top;

			@media #{$under-tablet} {
				min-height: 300px;
				height: 100%;
			}
		}

		.image-dt {
			//@media #{$large-device} {
				//display: none;
			//}

		}

		.image-mob {
			display: none;

			//@media #{$large-device} {
				//display: table;
			//}
		}//image-mob
	}//carousel-cell


	.flickity-page-dots {
		position: relative;
		bottom: 0;
		padding: 15px 0 0;
		margin: 0;

		.dot {
			background: $darkgrey;

			&.is-selected {
				background: $brand-color-two;
			}
		}

	}
}//home-carousel


.carousel-caption {
	padding-top: 15px;
	padding-bottom: 15px;
	background: $brand-color-two;
	text-align: left;
	font-weight: bold;
	margin: auto;
	width: 100%;
	position: relative;

	@include pad-hit

	@media #{$tablet} {
		padding-top: 15px;
		padding-bottom: 15px;
	}


	&__main-text {
		width: 100%;
		max-width:$max-width;
		margin: auto;
		display: table;

		span {
			color:white;
			font-family:$brand-font-one;
			font-size: 40px!important;
			line-height: 50px!important;
			font-weight: bold;
			text-decoration: none;

			@media #{$large-device} {
				margin: auto;
				text-align: center;
				display: table;
				font-size: 40px!important;
				line-height: 50px!important;
			}

			@media #{$small} {
				font-size: 26px!important;
				line-height: 30px!important;
			}
		}//span

		p {
			color: white;
			font-size: 24px!important;
			font-weight: 100;

			@media #{$large-device} {
				text-align: center;
			}

			@media #{$small} {
				font-size: 18px!important;
			}
		}
	}
}

#information-cta-block {
	.information-ctas {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin: 25px auto 50px;

		@media #{$large-device} {
			margin: 25px auto 0;
			flex-wrap: wrap;
		}

		.cta-block {
			width:22%;
			display: flex;
			flex-direction: column;
			align-items: center;
			overflow: hidden;
			position: relative;

			@media #{$large-device} {
				width: 48%;
				margin: 0 auto 50px!important;
				display: table;
				float: none;
			}

			@media #{$small} {
				width: 100%;
			}

			img {
				width: 100%;
				max-height: 125px;
				object-fit: contain;
			}

			&:first-child {
				margin-left: 0;
			}

			&:last-child {
				margin-right: 0;
			}

			span {
				color: $darkgrey;
				text-align: center;
				display: table;
				margin: 20px auto 0;
				font-weight: bold;
				font-size: 20px!important;
			}

			&__content {
				position: relative;
				transition:.25s all ease-in-out;

		
				p, ul {
					color: $darkgrey;
					text-align: center;
					transition: .25s all ease-in-out;
					display: table;
				}

				li {
					padding-left: 20px;
					position: relative;
					font-size: 18px!important;

					&:before {
						content:'';
						position: absolute;
						left: 0;
					}
				}

				a {
					margin: 25px auto 0;
					color: white;
					width: auto;
					padding: 5px 50px;
					border-radius: 5px;
					font-weight: normal;
					font-size: 18px!important;
					
				}

			}//content
		}//cta-block
	}//information-ctas
}//information-cta-block

#here-to-help {
	background: #e1e1e1;
	padding-top: 50px;
	padding-bottom: 50px;

	@media #{$large-device} {
		padding-top: 25px;
		padding-bottom: 25px;
	}

	h2 {
		color: $darkgrey;
		display: table;
		margin: auto;
		font-weight: 100;
		font-size: 30px!important;
		margin-bottom: 50px;

		@media #{$large-device} {
			@include pad-hit
			text-align: center;
			margin-bottom: 25px;
		}
	}

	.help-section {
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		@media #{$large-device} {
			flex-direction: column;
		}
		
		&__item {
			width: 30%;
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			position: relative;
			padding-bottom: 50px;

			@media #{$large-device} {
				width: 100%;
				margin-bottom: 50px;
			}

			&:last-child {
				@media #{$large-device} {
					margin-bottom: 0;
				}
			}

			h3 {
				color: $brand-color-one;
				font-weight: bold;
				display: table;
				font-size: 24px!important;
				margin-bottom: 5px;
			}

			p {
				font-weight: bold;
			}

			a {
				position: absolute;
				bottom: 0;
				max-width: 200px;
			}

		}
	}//help-section
}//here-to-help
#software-solutions-block {
	.software-solutions {
		display: flex;
		flex-direction: column;

		&__main {
			h2 {
				display: table;
				margin: auto;
			}

			p {
				display: table;
				margin: 5px auto 50px;
				text-align: center;
				@media #{$large-device} {
					font-weight: bold;
					text-align: left;
					margin-bottom: 25px;
				}
			}
		}

		&__details {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			margin-bottom: 50px;

			@media #{$large-device} {
				flex-direction: column;
			}

			&___divi {
				width: 20%;
				display: flex;
				justify-content: center;
				align-items: center;

				@media #{$large-device} {
					display: none;
				}

				.line {
					background: $grey;
					display: block;
					width: 1px;
					height: 200px;
				}
			}

			&___section {
				display: flex;
				flex-direction: column;
				align-items: center;
				width: 40%;

				@media #{$large-device} {
					width: 100%;
					margin-bottom: 25px;
				}

				&:last-child {
					@media #{$large-device} {
						margin-bottom: 0;
					}
				}
				
				h3 {
					position: relative;
					display: table;
					text-align: center;
					width: 100%;
					font-weight: bold;
					color: $darkgrey;
					overflow: hidden;
					margin-bottom: 25px;

					&:before, &:after {
						position: absolute;
						top: 51%;
						overflow: hidden;
						width: 49%;
						height: 1px;
						content: '\a0';
						background-color: $grey;
					}

					&:before {
						margin-left: calc(-50% - 20px);
						text-align: right;
					}

					&:after {
						text-align: right;
						margin-left: 20px;
					}
					
				}

				.logos {
					display: flex;
					flex-direction: row;
					flex-wrap: wrap;
					justify-content: space-between;

					&__logo {
						flex-basis: 45%;
						width: 45%;
						display: flex;
						align-items: center;
						justify-content: center;

						img {
							width: 100%;
							max-width: 175px;
							max-height: 75px;
							opacity: 1;
							transition: .25s all ease-in-out;

							&:hover {
								opacity: .5;
							}
						}
					}
				}//logos
			}//section
		}//details
	}//software-solutions
}//software-solutions-block

//iframe stuff
.iframe-video-section {
	display:none!important;
	opacity:0;
	position:fixed;
	z-index:999;
	top:0;
	background:rgba(0,0,0,0.75);
	width:100%;
	height:100%;
	display:block;
	transition:.25s opacity ease-in-out;
}
.iframe-video-open {
	display:block!important;
	opacity:1;
}
.iframe-video {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	margin-top:150px;

	@media #{$tablet} {
		width:90%;
	}
}
#main-vid {
	position: absolute;
	top: 0; 
	left: 0;
	width: 100%;
	height: 100%; 
	max-height:550px;
}
.vid-close-icon {
	position: absolute;
	top: -15px;
	right: -15px;
	cursor: pointer;
	width: 35px;
	height: 35px;
	line-height: 35px;
	background: $brand-color-one;
	color: white;
	text-align: center;
	border-radius: 30px;
	font-size: 25px;
	font-family: arial;
	font-weight: bold;
}
