/* =Footer
****************************************************************************************/

footer {
  position: relative;
}

.footer-blurb {
  max-width:250px;
  @media #{$large-device} {
    text-align: center;
    max-width: 350px;
    display: table;
    margin: 0 auto;
  }
}
.footer {
    background:$brand-color-three;
    color: white;
    display: table;
    width: 100%;

   .footer-container {
    display: table;
    width:100%;
    padding-top: 40px;

    @media #{$large-device} {
        padding-top: 20px;
    }
        &__left-side {
            width:100%;

            .footer-logo {
              max-width:200px;
              display: table;
              margin-bottom: 15px;

              @media #{$large-device} {
                margin: 0 auto 25px;
                padding-bottom: 25px;
                display: table;
                border-bottom: 1px solid white;
              }
            }

            span, a {
              color: white;
              font-size: 14px!important;

              @media #{$large-device} {
                font-size: 16px!important;
              }
            }

            @media #{$large-device} {
                width:100%;
                text-align: center;
                font-size: 16px!important;
            }

            

            .sitemap-mobile-link {
              display: none;
              position: relative;
              color: white;
              font-weight: bold;
              text-decoration: none;
              margin: 15px auto 0;

                @media #{$large-device} {
                  display: flex;
                }

                &:after {
                    content:">";
                    right: -15px;
                    font-weight: 400;
                    position: absolute;
                    transform: rotate(0deg);
                    transition: .25s transform ease-in-out;
                }
        
                &.active-sml {
                    &:after {
                        transform: rotate(90deg);
                    }
                }
            }

            &___menu {
                width: 100%;
                display: flex;
                justify-content: space-between;

                @media #{$large-device} {
                    //display: none;
                    flex-direction: column;
                }

                .demo-button {
                  display: table;
                  margin-top: 10px;
                }

                .corporate-links {
                    margin-bottom: 10px;
                }


                ul {
                    float: left;
                    width:auto;
                    padding:0 25px;

                    @media #{$large-device} {
                        width: 100%;
                        padding: 0;
                        display: flex;
                        flex-direction: column;
                        order: 0;
                        margin-bottom: 5px!important;
                    }

                    &:first-child {
                        padding-left: 0;
                        @media #{$large-device} {
                          width: auto;
                          display: table;
                          margin: auto;
                        }
                    }

                    &:last-child {
                      padding-right: 0;
                      @media #{$large-device} {
                        display: flex;
                        order: 1;
                      }
                    }

                    &.sitemap-ul {
                      @media #{$large-device} {
                        display: none;
                      }

                      &.show-su {
                        @media #{$large-device} {
                          display: table;
                        }
                      }
                    }
                    li {                        
                        font-size: 16px;
                        margin-bottom: 5px;
                        color: white;

                        @media #{$large-device} {
                          margin-bottom: 0;
                        }
                       
                        a {
                            color: white;
                            text-decoration: none;         
                            font-size: 14px;

                            @media #{$large-device} {
                                display: table;
                                margin: 0 auto;
                                font-size: 18px;
                            }

                            strong {
                                font-size: 16px;
                                display: table;
                                margin-top: 15px;
                                font-weight: bold;
                                text-transform: uppercase;

                            }
                        }
                    }
                }
            }
        }//left-side
    }//footer-container
}//footer

.amazon {
  @media #{$above-large-device}{
    margin-right: 50px;
  }
  img {
    max-width: 150px;
  }
}

#copyright {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: $max-width;
    margin: 50px auto;
    text-align: center;
    font-size: 14px;

    @media #{$large-device} {
      margin: 25px auto;
      flex-direction: column;
    }
    
    a {
        color: white;
        text-decoration: none;
        font-size: 14px;
    }
}

