/* Mixins */
@mixin pad-hit {
	@media screen and (max-width:1420px) {
		padding-left:15px;
		padding-right:15px;
	}
}

@mixin desktop-pad-hit {
	@media screen and (min-width:769px) and (max-width:1420px) {
		padding-left:15px;
		padding-right:15px;
	}
}

@mixin social-head {
	list-style: none;
	display: flex;
	align-items: center;

	@media #{$large-device} {
		display: flex;
		justify-content: flex-end;
		margin: 0;
	  }

	  li {
		float: left;
		color: white;
		display: flex;
		align-items: center;

		@media #{$large-device} {
		  display: table;
		  margin-right: 20px;
		}

		@media #{$small} {
			margin-right: 10px;
		}

		i {
            font-size: 30px;
            @media #{$large-device} {
              position: relative!important;
			}
			
			@media #{$small} {
				font-size: 25px;
			}

            &.fa-map-marker-alt {
			  font-size: 35px;
			  
			  @media #{$small} {
				  font-size: 30px;
			  }
            }
          }
	  }//li

	  a {
		color: white;
		font-size: 22px;
		text-decoration: none;
		display: flex;
		align-items: center;

		&:hover {
		  background: none;
		  text-decoration: none;
		}
		@media #{$large-device} {
		  color: white;
		  padding-right: 0;
		}
	  }//a

	  &__social {
		color:white;
		text-decoration: none;
		padding:0 10px;
		transition: .25s all ease-in-out;

		@media #{$large-device} {
		  display: none!important;
		}

		&:hover {
		  color: #b9b9b9;
		  background: none;
		}
	  }

	  a.facebook {
		border: 2px solid white;
		border-radius: 20px;
		width: 35px;
		height: 35px;
		margin-right: 10px;
		text-align: center;

		@media #{$large-device} {
			margin-right: 15px;
		}
		i {
		  font-size: 18px;
		}
	  }

	  a.facebook {
		padding-left:0;
		i {
		  margin: 0 auto 0 8px;
		}
	  }
	  a.email {
		padding-right:0;
	  }

	  span {
		color: white;
		display: table;
		padding-left: 35px;
		font-size: 18px!important;

		@media #{$large-device} {
		  display: none;
		}
	  }//span

	  &__phone {
		transition: .25s all ease-in-out;
	   
		span {
		  font-size: 20px!important;
		  padding-left: 10px!important;
		  transition: .25s all ease-in-out;
		}

		&:hover {
		  color: #b9b9b9;
		  span {
			color: #b9b9b9;
		  }
		}
	  }//__phone
}